.App {
    text-align: center;
  }
  
  .App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
  }
  
  .App-link {
    color: #61dafb;
  }
 
  .proj-box {
    display: block;
    position: relative;
    border: transparent;
    background-color: rgb(255, 255, 255,0.65);
    border-radius: 5px;
    padding: 60px;
    width: 60%;
    margin-left: 11px;
    margin-top: -150px;
    color: rgb(136, 109, 243);
    text-shadow: 2px 2px rgb(247, 215, 225);
    font-family: 'Courier New', Courier, monospace;
    align-items: center;
  }

  .small-box {
    display: inline-flex;
    position: relative;
    border: transparent;
    font-size: 15px;
  }

  .proj-pic {
    width: 200px;
    height: 100px;
    padding-left: 3px;
    margin-bottom: -3px;
  }

  .img-box {
    display: block;
    width: 50%;
  }
  
  .text-box {
    display: block;
    width: 50%;
    font-weight: 700;
    text-align: left;
    padding-left: 20px;
  }