.App {
    text-align: center;
  }
  
  .App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
  }
  
  .App-link {
    color: #61dafb;
  }
 
  .profile-box {
    display: block;
    position: relative;
    border: transparent;
    background-color: rgb(255, 255, 255,0.5);
    border-radius: 5px;
    padding: 60px;
    width: 50%;
    margin-left: 10px;
    margin-top: -150px;
    color: rgb(136, 109, 243);
    text-shadow: 2px 2px rgb(247, 215, 225);
    font-weight: 600;
    font-family: 'Courier New', Courier, monospace;
  }