#nav-bar {
    background-color: transparent;
    font-family: 'Courier New', Courier, monospace;
    font-size: 1rem;
    font-weight: normal;
    line-height: 1.5;
    box-sizing: border-box;
    border-radius: 10px;
    position: relative;
    height: auto;
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-left: 390px;
    align-items: left;
}

li.active a {
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 5px;
}

#nav {
    padding-left: 0%;
    margin: 0 auto;
    list-style: none;
    position: relative;
    background-color: transparent;
    cursor: pointer;
}

#nav li {
    float: left;
}

#nav a {
    display: block;
    box-sizing: content-box;
    width: 130px;
    height: 60px;
    text-decoration: none;
    font-size: 25px;
    font-weight: bolder;
    text-align: center;
    margin: 10px;
    padding-top: 20px;
}


#nav a:hover {
    color: rgb(255, 185, 234);
}

.fas {
    padding-left: 5px;
    font-size: 18px;
}